<template>
  <div>
    <h1 class="text-center">All Destinations</h1>
    <v-row>
      <v-col
        class="text-center"
        cols="6"
        sm="3"
        v-for="destination in destinations"
        :key="destination.id"
      >
        <router-link
          :to="{
            name: 'destinationDetails',
            params: { slug: destination.slug }
          }"
        >
          <h3>{{ destination.name }}</h3>
        </router-link>
        <router-link
          :to="{
            name: 'destinationDetails',
            params: { slug: destination.slug }
          }"
        >
          <v-img :src="require(`@/assets/${destination.image}`)"></v-img>
        </router-link>
      </v-col>
    </v-row>
  </div>
</template>

<script>
// @ is an alias to /src
import store from "@/store.js";
import axios from "axios";

// const APIURL = ''

export default {
  name: "Home",
  data: () => ({
    destinations: store.destinations
  }),
  created(){
    axios
      .all([
        axios.get('/api'),
        axios.get('/api/user'),        
      ])
      .then(
        axios.spread((home, user) => {
          console.log(home.data)
          console.log(user.data);         
        })
      )
      .catch(err => console.error(err));
  }
};
</script>

<style>
a {
  text-decoration: none;
  color: purple;
}
</style>
